import React, { Component } from 'react';
import { userAuth } from '../actions/users';
import { connect } from 'react-redux';

import { withCookies } from 'react-cookie'

export default function(ComposedClass, reload){
    class AuthCheck extends Component {
        state = {
            loading:true
        }

        componentDidMount(){
            this.props.dispatch(userAuth())
        }

        getSnapshotBeforeUpdate(prevProps) {
            if(!this.props.user.auth.isAuth){
                if(reload){
                    this.props.history.push('/login')
                }
            } else {
                if(reload === false) {
                    this.props.history.push('/profile')
                }
            }

            return { updateRequired: prevProps.user !== this.props.user }
        }

        componentDidUpdate(prevProps, prevState, snapshot){
            if(snapshot.updateRequired){
                this.setState({
                    loading:false
                })
            }
        }

        render(){
            if(this.state.loading){
                return (
                    <div style={{
                        width: "100%",
                        height: "100%",
                        top: 0,
                        left: 0,
                        position: "fixed",
                        display: "block",
                        backgroundColor: "#fff",
                        zIndex: "991",
                        textAlign: "center",
                    }} >
                        <div id="loading-image" className="uk-position-center uk-spinner uk-icon" uk-spinner="ratio: 4.5"></div>
                    </div>
                )
            }
            return  (
                <ComposedClass {...this.props} user={this.props.user} />
            )
        }
    }

    function mapStateToProps(state) {
        return {
            user:state.user
        }
    }
    return connect(mapStateToProps)(withCookies(AuthCheck))
}